.copyright__links a:hover {
  text-decoration: underline;
}

.accent {
  top: -20px;
  right: 20px;
  position: absolute;
  font-size: 22px;
  font-size: calc(14px + (22 - 14) * ((100vw - 575px) / (1199 - 575)));
}

@media (max-width: 575px) {
  .accent {
    font-size: 14px;
  }
}

@media (min-width: 1199px) {
  .accent {
    font-size: 22px;
  }
}
