@include font("ProximaNova", "ProximaNova-Light", 300);
@include font("ProximaNova", "ProximaNova-Regular");
@include font("ProximaNova", "ProximaNova-Bold", 700);

body {
	font-family: "ProximaNova";
	background: #f4f9ff;
	line-height: 1.4;
	// font-size: 200px
	// +css-lock(16, 50, 600, 1000)
	color: $text-color;
	@include css-lock(15, 18, 575, 1199);
	line-height: 1.4; }

.text-sm {
	@include css-lock(13, 15, 575, 1199); }

.text-md {
	@include css-lock(14, 17, 575, 1199); }

.text {
	@include css-lock(15, 18, 575, 1199); }

.text-xl {
	@include css-lock(16, 20, 575, 1199); }

.col-xl {
	min-width: 0; }


img {
	display: inline-block;
	max-width: 100%;
	height: auto; }

b {
	font-weight: 700; }

.btn {
	font-family: "ProximaNova";
	@include css-lock(16, 19, 575, 1199);
	padding: em(15, 19) em(37, 19);
	text-decoration: none; }

.btn-secondary {
	color: #fff;
	&:hover {
		color: #fff;
		background-color: $primary; } }


.btn-secondary:not(:disabled):not(.disabled):active {
	background-color: $primary;
	border-color: $primary;
	color: #fff; }

.out {
	overflow: hidden; }

.sect {
	position: relative;
	padding: 80px 0;
	@include r(1199) {
		padding: 60px 0 !important; }
	@include r(991) {
		padding: 45px 0 !important; }
	@include r(767) {
		padding: 30px 0 !important; }
	.container {
		position: relative;
		z-index: 40; }
	.texture {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		z-index: $texture-z-index;
		&__item {
			position: absolute;
			@include r(1199) {
				max-width: 50%; }
			@include r(991) {
				max-width: 30%; }
			@include r(767) {
				max-width: 20%; }
			@include r(575) {
				max-width: 10%; } } } }

.s-1 {
	.texture {
		&__item.ti-1 {
			position: absolute;
			right: -10px;
			top: -310px; } } }

.s-2 {
	padding-top: 136px;
	padding-bottom: 116px;
	.texture {
		&__item.ti-1 {
			left: 0;
			top: -20%; } } }

.s-3 {
	.texture {
		&__item.ti-1 {
			right: -3px;
			top: -63%; }
		&__item.ti-2 {
			left: 90px;
			bottom: 14%; }
		&__item.ti-3 {
			bottom: -15%;
			right: 30px; } } }

.s-4 {
	.texture {
		overflow: hidden;
		&__item.ti-1 {
			right: 2%;
			top: 6%; }
		&__item.ti-2 {
			bottom: -45%;
			left: -10px; }
		&__item.ti-3 {
			right: 0;
			bottom: -53%; }
		&__item.ti-4 {
			right: 15%;
			bottom: 13%; }
		&__item.ti-5 {
			left: -2%;
			top: 20%; } } }

.s-5 {
	.texture {
		&__item.ti-1 {
			left: -9%;
			top: -7%; }
		&__item.ti-2 {
			bottom: -27%;
			right: -17%; } } }

.s-6 {
	.texture {
		&__item.ti-1 {
			left: 0%;
			top: -12%; }
		&__item.ti-2 {
			bottom: 27%;
			right: -4%; } } }


.s-7 {
	padding-bottom: 0;
	.texture {
		&__item.ti-1 {
			left: 5%;
			top: -12%; } } }

.s-8 {
	padding-bottom: 0 !important;
	.texture {
		&__item.ti-1 {
			right: -2%;
			top: -26%; } } }

.s-9 {
	.texture {
		&__item.ti-1 {
			left: -3%;
			top: 36%; }
		&__item.ti-2 {
			right: -10%;
			top: 8%; }
		&__item.ti-3 {
			left: 35%;
			bottom: 15%; } } }

.s-10 {
	padding-top: 95px;
	.texture {
		&__item.ti-1 {
			left: 0%;
			top: 29%; }
		&__item.ti-2 {
			left: 43%;
			bottom: -17%; }
		&__item.ti-3 {
			right: -1%;
			top: -28%; } } }

.s-11 {
	padding-top: 100px;
	background-image: url(../img/map-bg.png);
	background-repeat: no-repeat;
	background-position: center top 55px; }

.h2 {
	font-weight: normal;
	@include css-lock(22, 36, 575, 1199);
	color: $primary;
	margin-bottom: em(70, 36); }

.h3 {
	color: $primary;
	font-weight: normal;
	@include css-lock(18, 24, 575, 1199);
	margin-bottom: em(32, 24); }

.btn.focus,
.btn:focus {
	box-shadow: none; }

.btn:not(:disabled):not(.disabled).active:focus,
.btn:not(:disabled):not(.disabled):active:focus {
	box-shadow: none; }

.main-header {
	box-shadow: 0.279px 15.998px 15.04px 0.96px rgba(37, 132, 239, 0.04);
	// overflow: hidden
	& > .container {
		@include r(991) {
			max-width: 100%; } } }

.main-header.fixed-top + .spacer {
	height: 86px; }

.header {
	&__nav {
		overflow: hidden; } }

.logo {
	&__img {
		margin-right: 20px; }
	&__text {
		font-weight: 700;
		letter-spacing: 1.2px;
		@include css-lock(20, 26, 575, 1199); } }

.main-nav {
	@include r(1199) {
		position: absolute;
		right: 0;
		top: 100%;
		background-color: #fff;
		padding: 8px 16px;
		box-shadow: 0.279px 15.998px 15.04px 0.96px rgba(37, 132, 239, 0.04);
		border-top: 1px solid $primary;
		transition: .5s;
		transform: translateX(200%);
		z-index: 10; }
	&.active {
		@include r(1199) {
			transform: translateX(0); } }
	&__list {
		@include css-lock(-5, -22, 575, 1199, margin-left);
		@include css-lock(-5, -22, 575, 1199, margin-right);
		@include r(1199) {
			flex-direction: column;
			margin-left: 0;
			margin-right: 0; } }
	li a {
		@include css-lock(14, 15, 575, 1199);
		color: $text-color;
		text-decoration: none;
		font-size: 15px;
		transition: color .3s;
		@include css-lock(5, 22, 575, 1199, padding-left);
		@include css-lock(5, 22, 575, 1199, padding-right);
		@include r(1199) {
			display: inline-block;
			font-size: 18px;
			padding: 16px 8px;
			width: 100%; } }
	li a:hover {
		color: $primary; } }

.toggle {
	width: 48px;
	height: 48px;
	position: relative;
	border-radius: 4px;
	background-color: transparent;
	&.active {
		.toggle__line:nth-child(1) {
			transform: translateY(10px) rotate(45deg); }
		.toggle__line:nth-child(2) {
			transform: translateX(-150%); }
		.toggle__line:nth-child(3) {
			transform: translateY(-10px) rotate(-45deg); } }
	&__line {
		position: absolute;
		height: 2px;
		background-color: $primary;
		left: 8px;
		right: 8px;
		top: 0;
		bottom: 0;
		margin: auto;
		transition: .3s; }
	&__line:nth-child(1) {
		top: -20px; }
	&__line:nth-child(3) {
		bottom: -20px; } }

.hero {
	position: relative;
	background-image: url(../img/hero.jpg);
	@include cover;
	padding-top: 80px;
	padding-bottom: 100px;
	@include r(1199) {
		padding-top: 70px; }
	&__btn {
		position: absolute;
		left: 0;
		right: 0;
		margin: auto;
		bottom: 18%;
		@include r(991) {
			bottom: 8%; }
		@include r(767) {
			bottom: 5%; } } }

.hero-item {
	text-align: center;
	@include r(767) {
		margin-bottom: 30px; }
	&__title {
		line-height: 1.2;
		text-align: center;
		@include css-lock(22, 30, 575, 1199);
		color: #fff;
		font-weight: normal;
		margin-bottom: em(30, 20); } }

.card-item {
	text-align: center;
	background-color: #fff;
	border-radius: 3px;
	box-shadow: 0.279px 15.998px 15.04px 0.96px rgba(37, 132, 239, 0.04);
	padding: 20px 25px;
	display: flex;
	flex-direction: column;
	height: calc(100% - 15px);
	margin-bottom: 15px;
	&--transparent {
		background-color: transparent;
		box-shadow: none;
		padding-right: 10px;
		padding-right: 10px; }
	&__icon {
		min-height: 160px;
		display: flex;
		justify-content: center;
		align-items: center; }
	&__title {
		@include css-lock(16, 17, 575, 1199);
		margin-bottom: em(12, 17);
		margin-left: -10px;
		margin-right: -10px; }
	&__text {
		margin-top: auto;
		word-wrap: break-word; } }

.sect.position-relative {
	.position-absolute {
		z-index: $img-z-index;
		display: flex;
		align-items: center; }
	.position-absolute.right {
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto; }
	.position-absolute.left {
		left: 0;
		top: 0;
		bottom: 0;
		margin: auto; } }

.bg-primary {
	color: #fff; }

.list {
	&--white {
		.list__item:before {
			border-color: $white;
			background-color: $primary; }
		.list__item:after {
			background-image: radial-gradient(circle, #fff 2px, transparent 2px); } }
	&--num {
		counter-reset: list; }
	&__item {
		@include css-lock(15, 17, 575, 1199);
		padding-bottom: em(32, 17);
		padding-left: em(40, 17);
		position: relative;
		&:before {
			position: absolute;
			content: '';
			left: 0;
			top: 8px;
			border: 3px solid $primary;
			width: 12px;
			height: 12px;
			border-radius: 50%;
			z-index: 10;
			background-color: #fff; }
		&:after {
			position: absolute;
			content: '';
			left: 4px;
			top: 8px;
			width: 4px;
			height: 100%;
			background-image: radial-gradient(circle, $primary 2px, transparent 2px);
			background-repeat: repeat-y;
			background-size: 4px 8px; } }
	&__item:last-child:after {
		display: none; }
	&__item--check:before {
		width: 15px;
		height: 9px;
		border: none;
		border-left: 4px solid $primary;
		border-bottom: 4px solid $primary;
		border-radius: 0;
		transform: rotate(-45deg); }

	&__item--num:before {
		counter-increment: list;
		content: counter(list, decimal-leading-zero);
		width: 29px;
		height: 29px;
		background-color: $primary;
		display: flex;
		justify-content: center;
		align-items: center;
		font-weight: 700;
		line-height: 1;
		color: #ffffff;
		font-size: 12px;
		left: -9px;
		top: -3px; } }

.collapse-item {
	background-color: rgb(229, 241, 255);
	border-radius: 3px;
	margin-bottom: 18px;
	padding: 15px 25px;
	&.active {
		.collapse-item__header {
			color: $primary;
			&:before {
				transform: translateY(25%) rotate(135deg); } } }
	&__header {
		transition: .3s;
		cursor: pointer;
		position: relative;
		padding-right: 25px;
		font-weight: 300;
		@include css-lock(15, 17, 575, 1199);
		line-height: 1.2;
		&:hover {
			color: $primary; }
		&:before {
			transition: .3s;
			position: absolute;
			content: '';
			right: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			border-left: 2px solid $primary;
			border-bottom: 2px solid $primary;
			width: 15px;
			height: 15px;
			transform: translateY(-25%) rotate(-45deg); } }
	&__body {
		font-weight: 300;
		padding-top: 10px;
		@include css-lock(14, 15, 575, 1199);
		display: none; } }

.s-5 {
	background-image: url(../img/road-bg.png);
	@include cover; }

.step-row {
	counter-reset: cite-item;
	position: relative;
	margin-top: -20px;
	margin-bottom: 70px;
	@include r(991) {
		margin-top: 0;
		margin-bottom: 40px; }
	&:before {
		position: absolute;
		content: '';
		background-image: radial-gradient(circle, $primary 2px, transparent 2px);
		background-repeat: repeat-y;
		background-size: 4px 8px;
		left: 0;
		right: 0;
		width: 4px;
		top: 50px;
		bottom: 50px;
		margin: auto;
		@include r(991) {
			display: none; } } }


.cite-item-wrap {
	@include r(991) {
		margin-bottom: 30px;
		padding-left: 15px !important;
		padding-right: 15px !important; }
	&:nth-child(even) {
		padding-left: 48px;
		.cite-item:before {
			left: 0;
			transform: translateX(-50%) rotate(45deg); }
		.cite-item:after {
			left: -48px;
			transform: translateY(-25%) translateX(-50%); } }
	&:nth-child(odd) {
		padding-right: 48px;
		.cite-item:before {
			right: 0;
			transform: translateX(50%) rotate(45deg); }
		.cite-item:after {
			right: -48px;
			transform: translateY(-25%) translateX(50%); } } }

.cite-item {
	border-radius: 20px;
	padding: 23px;
	background-color: $primary;
	color: #fff;
	@include css-lock(14, 16, 575, 1199);
	position: relative;
	line-height: 1.3;
	&:before {
		position: absolute;
		content: '';
		width: 19px;
		height: 19px;
		top: 30%;
		background-color: $primary;
		@include r(991) {
			display: none; } }
	&:after {
		top: 30%;
		counter-increment: cite-item;
		position: absolute;
		content: counter(cite-item, decimal-leading-zero);
		@include css-lock(18, 22, 575, 1199);
		color: $primary;
		background-color: #fff;
		border-radius: 50%;
		border: 3px solid $primary;
		width: em(43, 22);
		height: em(43, 22);
		display: flex;
		justify-content: center;
		align-items: center;
		@include r(991) {
			display: none; } } }


.example {
	margin-bottom: 30px;
	background-color: #edf2f8;
	border-radius: 4px;
	height: calc(100% - 30px);
	display: flex;
	flex-direction: column;
	border-radius: 4px;
	overflow: hidden;
	&__list {
		background: url("../img/examples/backchart.png");
		@include cover;
		padding: 18px 20px;
		padding-bottom: 0;
		li {
			color: #fff;
			font-size: 12px;
			display: flex;
			align-items: flex-start;
			position: relative;
			margin-bottom: 7px; }
		li:before {
			position: absolute;
			content: '';
			left: 56px;
			height: 2px;
			right: 0;
			bottom: 5px;
			background-color: #4995eb; }
		li:last-child {
			font-size: 10px;
			opacity: .5;
			&:before {
				display: none; } }
		li span {
			@include css-lock(22, 26, 575, 1199);
			margin-right: 10px;
			min-width: 45px; } }
	&__text {
		padding: 20px 15px;
		font-size: 12px;
		line-height: 1.9; }
	&__meta {
		margin-top: auto;
		display: flex;
		padding: 15px;
		align-items: center;
		justify-content: space-between; }
	&__meta img {
		max-width: 130px; }
	&__user {
		span {
			display: block;
			font-size: 12px; } } }



.contact {
	padding-left: 70px;
	position: relative;
	margin-bottom: 30px;
	@include r(767) {
		padding-left: 0;
		padding: 30px 0; }
	&__flag {
		position: absolute;
		left: 0;
		top: 0;
		@include r(757) {
			top: -10px; } }
	&__title {
		line-height: 1;
		@include css-lock(18, 24, 575, 1199);
		margin-bottom: em(20, 24); }
	&__list {
		li {
			@include css-lock(16, 18, 575, 1199);
			line-height: 2;
			span:first-child {
				display: inline-block;
				min-width: 110px; }
			a {
				color: #fff;
				text-decoration: none; }
			a:hover {
				text-decoration: underline; } } } }

.main-footer {
	padding-top: 40px;
	padding-bottom: 40px;
	border-top: 1px solid #3d92f1; }

.copyright {
	&__links {
		margin-top: 20px;
		a {
			@include css-lock(14, 15, 575, 1199);
			text-decoration: none;
			color: #fff; }
		a:hover {
			text-decoration: underline; } } }

.accent {
	top: -20px;
	right: 20px;
	position: absolute;
	@include css-lock(14, 22, 575, 1199); }







